//-------------------------------------------------------------------------------------------------
// IMPORTS
import { motion } from "framer-motion";
import React from "react";
import { Link } from "react-router-dom";
import "semantic-ui-css/semantic.min.css";
import "../../styles/index.css";
import "../../styles/navbar.css";
//-------------------------------------------------------------------------------------------------
// CSS INLINE STYLING USING TAILWIND
const hover_style =
  "text-black  text-blue-200 bg h-full hover:text-bold hover: text-black  hover: rounded-3xl text-2xl from-cyan-100 hover:text-bold ";
const header_style =
  "   badge rounded-pill text-gray-300 font-mono text-white h-full rounded-2x1 border-spacing-2 nav-link active ";
const background =
  "mt-1 mb-3 scale-95 hover:scale-100 p-2 bg-white rounded-lg border-7 border-red-500  border-opacity-50  shadow-2xl ";

//-------------------------------------------------------------------------------------------------

function Navbar(props) {
  return (
    <>
      <div className="sticky top-0 z-50 bg-transparent scale-60 navbar_style">
        <div className={background}>
          <Link className={header_style} to="/">
            <motion.div
              whileHover={{
                boxShadow: "1px 1px 10px rgba(0, 0, 0, 0.4)",
                scale: 1.05,
                transition: { duration: 1 },
              }}
            >
              <button
                className={`${hover_style} nav_sty drop-shadow-xl r-96 px-5 `}
              >
                JOURNEY 🚀{" "}
              </button>
            </motion.div>
          </Link>
          <Link className={header_style} to="/Project/">
            <motion.div
              whileHover={{
                boxShadow: "1px 1px 10px rgba(0, 0, 0, 0.2)",
                scale: 1.05,

                transition: { duration: 1 },
              }}
            >
              <button
                className={`${hover_style} nav_sty drop-shadow-xl l-96 px-5`}
                variant="outline success"
              >
                PROJECTS 📕{" "}
              </button>
            </motion.div>
          </Link>
        </div>{" "}
      </div>
    </>
  );
}
export default Navbar;
